import React from 'react';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import ButtonAppBar from '../AppBar/AppBarS';
import { Avatar } from '@material-ui/core';
import ImageAvatars from '../Avatar/Avatar';
import DownloadResume from '../Download_Resume/DownloadResume';

export default function SimpleMediaQuery() {
  const small = useMediaQuery('(max-width:1024px)');
  const large =useMediaQuery('(min-width:1025px)')

  return small?
  <div>
      <ButtonAppBar/>
      <ImageAvatars/>
      <DownloadResume/>
     
  </div>
  :large?
  <div>
      <ButtonAppBar home={"Home"}/>
      <ImageAvatars/>
      <DownloadResume/>
     
  </div>
  :
  <div>

  </div>
}
